<template>
  <div class="p-6">
    <h1 class="text-4xl font-bold text-center mb-8">Upcoming Events</h1>

    <!-- Display the current page of events -->
    <div class="space-y-8">
      <div
        v-for="event in paginatedEvents"
        :key="event.id"
        class="bg-white border border-gray-300 rounded-lg p-6 shadow-md cursor-pointer"
        @click="goToEvent(event.id)"
      >
        <div class="flex items-center mb-4">
          <h2 class="text-2xl font-semibold flex-1">{{ event.title }}</h2>
          <span
            :class="{
              'bg-green-500 text-white': isUpcoming(event.date),
              'bg-gray-500 text-white': !isUpcoming(event.date)
            }"
            class="inline-flex items-center px-3 py-1 text-sm font-medium rounded-full"
          >
            {{ isUpcoming(event.date) ? 'Upcoming' : 'Completed' }}
          </span>
        </div>
        <p class="text-sm text-gray-500 mb-4">{{ formatDate(event.date) }}</p>
        <p class="text-lg mb-4">{{ event.description }}</p>
        <div class="flex space-x-4">
          <img 
            v-if="event.image" 
            :src="event.image.startsWith('http') ? event.image : `${event.image}`" 
            alt="Event Image" 
            class="h-40 w-40 object-cover rounded-lg" 
          />
        </div>
      </div>
    </div>

    <!-- Pagination Controls -->
    <div class="flex justify-center mt-8 space-x-4">
      <button
        @click="currentPage--"
        :disabled="currentPage === 1"
        class="bg-gray-300 px-4 py-2 rounded hover:bg-gray-400"
      >
        Previous
      </button>
      <button
        @click="currentPage++"
        :disabled="currentPage === totalPages"
        class="bg-gray-300 px-4 py-2 rounded hover:bg-gray-400"
      >
        Next
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Events',
  data() {
    return {
      events: [
        // Sample events data with ids for routing
        {
          id: 'eid_ul_fitr_2025',
          title: "Eid Ul-Fitr Party",
          date: '2025-04-15',
          description:
            'Come and celebrate with us!',
          image:
            '/events/eid_ul_fitr_2025/Eid-ul-Fitr 2025 Event.pdf.jpg'
        },
        {
          id: 'womens_workshop_2025',
          title: "Women's health workshop",
          date: '2025-04-16',
          description:
            'Learn how to manage health conditions from experts!',
          image:
            '/events/womens_workshop_2025/Self Management course for women.jpg'
        },
        {
          id: 'help_nhs_bengali_2025',
          title: 'Help the NHS!',
          date: '2025-04-23',
          description:
            'Come and help NHS to improve medical information for Bengali speakers',
          image:
            '/events/help_nhs_bengali_2025/NHS Information Events Leaflet .jpg'
        },
        {
          id: 'summer_trip_24',
          title: 'Summer Trip 24',
          date: '2024-08-13',
          description:
            'A trip to Bournemouth beach for the community. Bring family and friends!',
          image:
            '/events/summer_trip_24/SummerDayTrip24Leaflet.jpg'
        },
        {
          id: 'cervical_cancer_2025',
          title: 'Cervical Cancer Screening Workshop',
          date: '2025-01-27',
          description:
            'Learn about cervical cancer screening!',
          image:
            '/events/cervical_cancer_2025/CervicalCancerScreening.jpg'
        },
        // Add more events as needed
      ],
      currentPage: 1,
      eventsPerPage: 5,
    };
  },
  computed: {
    sortedEvents() {
      return this.events.slice().sort(
        (a, b) => new Date(b.date) - new Date(a.date)
      );
    },
    totalPages() {
      return Math.ceil(this.sortedEvents.length / this.eventsPerPage);
    },
    paginatedEvents() {
      const start = (this.currentPage - 1) * this.eventsPerPage;
      const end = start + this.eventsPerPage;
      return this.sortedEvents.slice(start, end);
    },
  },
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      });
    },
    isUpcoming(date) {
      return new Date(date) > new Date();
    },
    goToEvent(id) {
      this.$router.push(`/events/${id}`);
    },
  },
};
</script>

